// Custom mixins

// Changes background style to cover, centraly aligned with no repeat on the image.
.cover-bg(){
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
}

// Absolutly positions an element to be as large as the nearest relativly positioned ancestral element.
// The left value is to correct the position when the parent has a padding
.abs-cover(@left:0;){
	position: absolute;
	top: 0;
	bottom: 0;
	left: @left;
	right: 0;
	width: 100%;
	height: 100%;
}

// Adds a constant apect ratio to the element. The width must be given by the parent element.
// baseW is the base width, baseH is the base height
.aspect-ratio(@baseW, @baseH){
	height: 0;
	width: 100%;
	padding-bottom: (100% * (@baseH / @baseW)) ;
	position: relative;
	>*{
		.abs-cover(0);
	}
}


// Allows clicking through an element but not children
.clickThrough(){
	pointer-events: none;
	>*{
		pointer-events: auto;
	}
}


/* Added by Bills - letter spacing mixin for various texts on site */

// Allows clicking through an element but not children
.letterspacing(){
	letter-spacing: -0.6px;
}